/* select Network */

.networksOptions {
  position: absolute;
  background-color: #00CCFF;
  margin-top: 2px;
  width: 105px;
  padding: 10px 5px 5px 10px;
  z-index: 99;
  border-radius: 6px;
  color: #000000;
}

.networksOptions h3 {
  font-weight: bold;
  font-size: 20px;
  cursor: pointer;
}

.networkCross {
  position: absolute;
  right: 10px;
  top: 0;
  font-size: 16px;
  font-weight: bold;
  color: red;
  cursor: not-allowed;
}

@media (max-width: 1000px) {
  .networksOptions {
    width: 80px;
  }
}



/* Sign IN BOX */
.signInBox {
  /* background-color: red; */
  background-image: url(https://picsum.photos/id/1080/6858/4574), linear-gradient(rgb(219, 166, 166), rgb(0, 0, 172));
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  width: 100%;
}

.containersignInBox {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.boxsignInBox {
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 5px;
  font-family: sans-serif;
  text-align: center;
  line-height: 1;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(20px);
  /* max-width: 50%;
  max-height: 50%; */
  height: 100%;
  width: 100%;
  padding: 20px 40px;
}

