.input-fel {
  width: 350px;
  border: none;
  padding-bottom: 7px;
  border-bottom: 2px solid rgb(163, 163, 163);
  /* for moving of feild */
  transition-property: width;
  transition-duration: 1s;
  background: transparent;
  /* transition-delay: 1s; */
  height: 30px;
  border-radius: 5px;
  font-size: 16px;
  outline: none;
  border: none;
  border-bottom: 2px solid white;
  color: white;
  background: transparent;
}

.input-fel:hover {
  border-bottom: 2px solid white;
  color: white;
  /* for moving of feild */
  width: 230px;
}

.input-fel:active {
  border-bottom: 2px solid white;
  color: white;
  /* for moving of feild */
  width: 230px;
}

.input-fel:focus {
  outline: none;
  border: none;
  border-bottom: 2px solid white;
  color: white;
  background: transparent;
}

.forgot-pass {
  font-size: 10px;
  margin-left: 65px;
  color: rgb(74, 0, 192);
  display: inline-block;
}

.forgot-passw {
  font-size: 10px;
  margin-right: 7px;
  color: rgb(74, 0, 192);
  display: inline-block;
}

.log-in {
  padding: 11px 29px;
  /* background-color: rgb(97, 50, 158); */
  background-color: #09993b;
  border: 1px solid rgb(185, 185, 185);
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  font-size: 18px;
}

.log-in-loader {
  padding: 11px 29px;
  /* background-color: rgb(97, 50, 158); */
  background-color: rgb(40, 0, 93);
  color: #fff;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  font-size: 12px;
}

.log-in:hover {
  background-color: rgb(231, 231, 231);
  color: black;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  border: 1px solid rgb(40, 0, 93);
}

